.time-table{
  display: grid;
  grid-template-columns: calc(100% - 25em) 25em;
  min-height: $vh;
  &__col{
    padding-bottom: 4em;
  }
  &__col2{
    background: #f5f6e8;
    padding-bottom: 4em;
  }
}

.time-table-header{
  padding: 1em 2em;
  position: relative;
  &__title{
    font-size: 2.5em;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: $darkBlue;
    text-align: center;
  }
  &__clock{
    position: absolute;
    left: 2em;
    top: 50%;
    transform: translateY(-50%);
    font-weight: 700;
    span{
      font-size: 3.5em;
    }
  }
}

.time-table-content{
  overflow: hidden;
  table{
    width: 100%;
    border-spacing: 0;
  }
  th,td{
    padding: 1em 2em;
  }
  th{
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    background: #85c2e1;
    color: #fff;
    span{
      font-size: 1.875em;
    }
  }
  td{
    border-bottom: 1px solid #c4c4c4;
    span{
      font-size: 1.75em;
    }
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5){
      text-align: center;
    }
  }
  .not-in-time td{
    color: #868686;
    background: #eaeaea;
  }
  .swiper-pagination{
    background: #e8f3f9;
    position: static;
    width: 100%;
    text-align: center;
    padding: 1em;
  }
}

.running-line{
  z-index: 5;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 1em;
  background: #fc6669;
  color: #fff;
  white-space: nowrap;
  display: flex;
  span{
    animation: 40s linear infinite running-line;
    font-size: 2em;
    display: block;
    margin-right: .25em;
  }
}

@keyframes running-line {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}

