.sidebar{
  padding: 1em 2em;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__date{
    font-size: 2.25em;
    line-height: 120%;
    font-weight: 700;
    text-align: center;
  }
  &__contacts{
    @include grid100gap(2em);
  }
  &__doctors{
    text-align: center;
    &-title{
      font-size: 1.5em;
      line-height: 120%;
      font-weight: 700;
    }
  }
}

.contact-item{
  display: grid;
  grid-template-columns: 1.75em calc(100% - 2.25em);
  align-items: center;
  gap: .5em;
  &__icon{
    img{
      width: 100%;
      display: block;
    }
  }
  &__title,
  &__desc{
    font-size: 1.75em;
    line-height: 130%;
  }
  &__desc{
    font-weight: 700;
  }
}

.doctors-slider{
  margin-top: 3.125em;
  overflow: hidden;
}

.doctor-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  &__img{
    width: 12em;
    height: 16.5em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__title{
    font-size: 1.5em;
    line-height: 120%;
    text-transform: uppercase;
    font-weight: 700;
    margin-top: 1em;
  }
  &__desc{
    margin-top: 1em;
    font-size: 1.25em;
    line-height: 120%;
    color: #868686;
  }
}

.banners{
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  z-index: 6;
  overflow: hidden;
  .swiper-slide{
    height: $vh;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
}

.screen-box{
  &._hide-banner{
    .banners{
      opacity: 0;
      pointer-events: none;
    }
  }
}
